@import './utils/variables';

.card {
  box-sizing: border-box;
  display: inline-block;
  width: $card-width;
  border-radius: 5px;
  border: 1px #f3f3f3 solid;

  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: scale(1.2);
  }

  &__container {
    margin-right: 16px;
    margin-left: 16px;
  }

  &__product-imac {
    display: flex;
    justify-content: center;
  }

  &__cost {
    color: $main-color;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__img {
    margin-top: 32px;
    margin-bottom: 40px;
    width: 160px;
    height: 134px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__price-title {
    color: #616070;
    font-size: 12px;
    line-height: 18px;
  }

  &__product-code {
    margin-bottom: 16px;
    color: #616070;
    font-size: 10px;
    line-height: 14px;
  }

  &__product-name {
    margin: 0;
    margin-bottom: 4px;
    color: $main-color;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__review {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & .reviews {
      font-family: Roboto, sans-serif;
      color: $main-color;
      font-size: 10px;
      line-height: 14px;
    }
  }
  &__button {
    height: 40px;
    width: 100%;
    padding: 0;
    font-family: Roboto, sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
    background: #00acdc;
    border-radius: 5px;
    border-style: none;
    margin-bottom: 16px;
  }

  &__button:hover {
    background-color: #fff;
    color: #00acdc;
    border: 1px #00acdc solid;
  }
}
